import React, { Component} from "react";
import '../../Project.css'
class Project extends Component {
    state = {};
    render() {
        return(
            <div className="org-project">
                <div className="project-header">
                    {this.props.projectIcon ? <div className="project-icon">
                        <img src={this.props.projectIcon}/>
                        </div> : <div className="project-icon"></div>}
                    <div className="project-title-context">
                        <div className="project-title">{this.props.projectTitle}</div>
                        <div className="project-year">{this.props.projectYear}</div>
                        <div className="project-tech-stack">
                        <div>
                            {this.props.projectTechStack.map(element => <div className="tech-stack-item"> {element} </div>)}
                        </div>
                        </div>
                    </div>
                </div>
                <div className="project-detail">
                    {this.props.children}
                </div>
            </div>
        );
    }
}

export default Project;