import React, { Component} from "react";
import TwitterIcon from "../icons/twitter.png";
import GithubIcon from "../icons/github.png";
import LinkedIcon from "../icons/Linkedin-Logo.png";
import GmailIcon from "../icons/gmail.png";
class Contact extends Component {
    state = {};
    render() {
        return(
            <div className="page contact-page" id="contact">
                <div className="contact-container">
                    <span className="contact"><a href="https://www.linkedin.com/in/antoarundominic/" target="_"> <img className="contact-icons" src={LinkedIcon}/></a></span>
                    <span className="contact"><a href="https://github.com/antoarundominic"><img className="contact-icons github" src={GithubIcon}/></a></span>
                    <span className="contact"><a href="https://twitter.com/antoarundominic"><img className="contact-icons" src={TwitterIcon}/></a></span><br/>
                    <span className="contact email" title="Click to copy" onClick={() => {navigator.clipboard.writeText("antoarundominic@gmail.com")}}>
                        <img className="contact-icons gmail" src={GmailIcon}/>antoarundominic@gmail.com
                    </span>
                </div>
            </div>
        );
    }
}

export default Contact;